import { Box, FormControl, InputLabel, Select, MenuItem, Stack } from '@mui/material';
import { useTheme } from '@mui/material';
import { isValidArray } from '../../../../../Utils/UtilFunctions';

export const SimplePicker = (props) => {
  const { icon, label, value, options, handleChange, disabled, isExtraSmall, ...otherProps } = props;
  const theme = useTheme();

  return (
    <Stack
      direction="row"
      gap={icon ? 1 : 0}
      alignItems="center"
      {...otherProps}
    >
      <Box
        aria-hidden={true}
        sx={{
          '& .MuiSvgIcon-root': {
            color: disabled ? theme.palette.text.secondary : theme.palette.primary.main,
            verticalAlign: "middle"
          }
        }}
      >
        {icon}
      </Box>

      <FormControl
        fullWidth
        size='small'
        disabled={disabled}
        sx={{
          minWidth: "5rem",
          marginLeft: "0 !important",
          "& .MuiInputBase-root": {
            [theme.breakpoints.down('sm')]: {
              fontSize: isExtraSmall ?? "0.75rem"
            }
          }
        }}
      >
        <InputLabel id={`${label}-picker-label`}>{label}</InputLabel>
        <Select
          labelId={`${label}-picker-label`}
          id={`${label}-picker`}
          value={value}
          label={label}
          onChange={handleChange}
        >
          {isValidArray(options) &&
            options.map((option) => (
              <MenuItem key={option.value} value={option.value} is>
                {option.label}
              </MenuItem>
            ))}
        </Select>
      </FormControl>
    </Stack >
  );
};
